const tools = [
    "Next.js", "NestJS", "TypeScript", "PostgreSQL",
    "Python", "PyTorch", "Docker", "Unity",
    "Spring", "Redis", "Prisma", "Tailwind"
]

export function ScrollingStack() {
    return (
        <section className="py-8 bg-[#0d1117] border-y border-gray-800/50 overflow-hidden select-none relative">
            {/* –ì—Ä–∞–¥–∏–µ–Ω—Ç–Ω–∞—è –º–∞—Å–∫–∞ –¥–ª—è –º—è–≥–∫–∏—Ö –∫—Ä–∞–µ–≤ */}
            <div className="absolute inset-y-0 left-0 w-20 bg-gradient-to-r from-[#0d1117] to-transparent z-10"></div>
            <div className="absolute inset-y-0 right-0 w-20 bg-gradient-to-l from-[#0d1117] to-transparent z-10"></div>

            <div className="flex w-max animate-marquee">
                {/* –†–µ–Ω–¥–µ—Ä–∏–º —Å–ø–∏—Å–æ–∫ –¥–≤–∞–∂–¥—ã –¥–ª—è –±–µ—Å–∫–æ–Ω–µ—á–Ω–æ–≥–æ —Ü–∏–∫–ª–∞ */}
                {[...tools, ...tools].map((tool, i) => (
                    <div key={i} className="flex items-center">
            <span className="text-xl md:text-2xl font-bold text-gray-500/30 mx-6 hover:text-rose-500/60 transition-colors cursor-default tracking-tight">
              {tool}
            </span>
                        {/* –ú–∞–ª–µ–Ω—å–∫–∏–π —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å –º–µ–∂–¥—É —Å–ª–æ–≤–∞–º–∏ */}
                        <span className="w-1 h-1 rounded-full bg-gray-800"></span>
                    </div>
                ))}
            </div>
        </section>
    )
}