export const lsCommand: Command = {
    name: 'ls',
    description: '–ü–æ–∫–∞–∑–∞—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ —Ç–µ–∫—É—â–µ–π –ø–∞–ø–∫–∏',
    execute: (args, terminal) => {
        return terminal.listDir()
    }
}

export const cdCommand: Command = {
    name: 'cd',
    description: '–ü–µ—Ä–µ–π—Ç–∏ –≤ –ø–∞–ø–∫—É (cd foldername –∏–ª–∏ cd ..)',
    execute: (args, terminal) => {
        if (!args[0]) return ''
        return terminal.changeDir(args[0])
    }
}

export const catCommand: Command = {
    name: 'cat',
    description: '–ü—Ä–æ—á–∏—Ç–∞—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ —Ñ–∞–π–ª–∞',
    execute: (args, terminal) => {
        if (!args[0]) return 'Usage: cat <filename>'
        return terminal.readFile(args[0])
    }
}

export const pwdCommand: Command = {
    name: 'pwd',
    description: '–ü–æ–∫–∞–∑–∞—Ç—å —Ç–µ–∫—É—â–∏–π –ø—É—Ç—å',
    execute: (args, terminal) => {
        return terminal.getPathString()
    }
}

