export const helpCommand: Command = {
    name: 'help',
    description: '–ü–æ–∫–∞–∑—ã–≤–∞–µ—Ç –≤—Å–µ –¥–æ—Å—Ç—É–ø–Ω—ã–µ –∫–æ–º–∞–Ω–¥—ã',
    execute: (args, terminal) => {
        const commands = Array.from(terminal.getCommands().values())
        return `
Available commands:

${commands.map(cmd => `  ${cmd.name.padEnd(10)} ${cmd.description}`).join('\n')}

Tips:
  - –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ 'ls' —á—Ç–æ–±—ã —É–≤–∏–¥–µ—Ç—å —Ñ–∞–π–ª—ã
  - –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ 'cat <—Ñ–∞–π–ª>' —á—Ç–æ–±—ã –ø—Ä–æ—á–∏—Ç–∞—Ç—å –∏—Ö
  - –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ 'neofetch' –¥–ª—è –∫—Ä–∞—Å–æ—Ç—ã
`
    },
}

// --- –°–¢–ê–†–´–ï (–ú–ê–¢–ï–ú–ê–¢–ò–ß–ï–°–ö–ò–ï) –ö–û–ú–ê–ù–î–´ ---
// (–û—Å—Ç–∞–≤–ª—è–µ–º –∏—Ö, —á—Ç–æ–±—ã —Ñ—É–Ω–∫—Ü–∏–æ–Ω–∞–ª –Ω–µ –ø—Ä–æ–ø–∞–ª, –Ω–æ –æ–Ω–∏ —Ç–µ–ø–µ—Ä—å –≤—Ç–æ—Ä–∏—á–Ω—ã)

export const clearCommand: Command = {
    name: 'clear',
    description: '–û—á–∏—Å—Ç–∏—Ç—å —Ç–µ—Ä–º–∏–Ω–∞–ª',
    execute: (args, terminal) => {
        terminal.clearHistory()
        terminal.clearUICallBack?.()
        return ''
    },
}

export const echoCommand: Command = {
    name: 'echo',
    description: '–í—ã–≤–µ—Å—Ç–∏ —Ç–µ–∫—Å—Ç',
    execute: (args) => args.join(' '),
}

export const dateCommand: Command = {
    name: 'date',
    description: '–¢–µ–∫—É—â–∞—è –¥–∞—Ç–∞',
    execute: () => new Date().toLocaleString('ru-RU'),
}

// –ú–∞—Ç–µ–º–∞—Ç–∏–∫—É –º–æ–∂–Ω–æ —Å–æ–∫—Ä–∞—Ç–∏—Ç—å –∏–ª–∏ –æ—Å—Ç–∞–≤–∏—Ç—å –∫–∞–∫ –µ—Å—Ç—å
const safeEval = (fn: () => number) => {
    try { return String(fn()) } catch { return 'Error' }
}

export const sumCommand: Command = {
    name: 'sum',
    description: '–°–ª–æ–∂–µ–Ω–∏–µ (sum 2 2)',
    execute: (args) => safeEval(() => args.map(Number).reduce((a, b) => a + b, 0))
}

export const whoamiCommand: Command = {
    name: 'whoami',
    description: '–ö—Ç–æ —è?',
    execute: () => 'root'
}

export const sudoCommand: Command = {
    name: 'sudo',
    description: '–í—ã–ø–æ–ª–Ω–∏—Ç—å –æ—Ç –∏–º–µ–Ω–∏ —Å—É–ø–µ—Ä–ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è',
    execute: () => 'Permission denied: you are viewing a portfolio, not a server :)'
}

// --- –°–ë–û–†–ö–ê –í–°–ï–• –ö–û–ú–ê–ù–î ---

export const commandMap = {
    ls: lsCommand,
    cd: cdCommand,
    cat: catCommand,
    pwd: pwdCommand,
    neofetch: neofetchCommand,
    help: helpCommand,
    clear: clearCommand,
    echo: echoCommand,
    date: dateCommand,
    sum: sumCommand,
    whoami: whoamiCommand,
    sudo: sudoCommand
} as const

export const allCommands = Object.values(commandMap)
export const commands = Object.keys(commandMap)